import { useRef } from "react";
import { postPost } from "../api/api";
import { useUserStore } from "../stores/userStore";
import { useNavigate } from "react-router-dom";

function PostWrite() {
  const { user } = useUserStore();
  const navigate = useNavigate();
  const textarea = useRef();

  const handleResizeTextArea = (e) => {
    textarea.current.style.height = "auto"; //height 
    textarea.current.style.height = textarea.current.scrollHeight + "px";
  };

  const handleSubmit = async (e) => {
    e.preventDefault(); //    
    const title = e.target.title.value;
    const content = e.target.content.value;
    const writer = user.id;
    //   
    try {
      const res = await postPost({ title, content, writer });
      navigate(`/posts/${res}`);
    } catch (error) {
      console.error("    :", error);
    }
  };

  const handleCancle = () => {
    if (!window.confirm("   .\n ?")) return;
    navigate(-1);
  };

  return (
    <section className='sec write-sec'>
      <div className='container'>
        <h2 className='title text-center'></h2>
        <form className='form d-flex flex-column gap-2' onSubmit={handleSubmit}>
          <input type='text' name='title' className='post-title' placeholder='' required></input>
          <textarea name='content' className='post-content' cols={1} rows={1} placeholder='-  -' onChange={handleResizeTextArea} ref={textarea} required></textarea>
          <div className='btn-wrapper d-flex justify-content-end gap-2'>
            <button type='button' className='btn cancleBtn' onClick={handleCancle}>
              
            </button>
            <button type='submit' className='btn submitBtn'>
              
            </button>
          </div>
        </form>
      </div>
    </section>
  );
}

export default PostWrite;
